.SideDrawer {
  height: 100vh;
  width: 55vw;
  position: fixed;
  right: 5vw;
  transform: translateX(110%);
  transition: transform 0.3s ease-out;
  z-index: 10;
}

@media only screen and (max-width: 800px) {
  .SideDrawer {
    width: 80vw;
  }
}

.SideDrawer.Open {
  transform: translateX(0);
}